.msgnoresult{
    text-align: center;
    font-size: 25px;
    margin-bottom: 50px;
}

.equis{
    width: 200px;
    height: 200px;
    background-color: rgb(246, 48, 9);
    clip-path: polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%);
    margin: auto;
    animation: equis 2s ease-in-out infinite; 
    
}

@keyframes equis {
    0%, 100%{
        transform: scale(1);
        transition: transform 2s;
    }
    50%{
        transform: scale(1.3);
        transition: transform 2s;
    }
}

@media (max-width: 500px){
    .equis{
        width: 140px;
        height: 140px;
    }
}